import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import Headroom from 'react-headroom';
import Navigator from '../components/Navigator';
import HeaderMaster from '../components/Masthead';
import HeaderChild from '../components/Childhead';
import Footer from '../components/footer';

import '../scss/styles.scss';

class TemplateWrapper extends React.Component {
  
  render() {
    let background = null;
    const isHome = this.props.location.pathname == `/`;
    if (isHome) background = <HeaderMaster/>;
    else background = <HeaderChild currentpage={this.props.location.pathname}/>;

    return (
      <div>
        <Helmet
        title="Sysforge: Madhav Shandilya's Portfolio!"
        meta={[
          { name: 'description', content: 'M Shandilya Portfolio' },
          { name: 'keywords', content: 'Madhav Shandilya, Github, Sysforge' },
        ]}
      />
      <Headroom disableInlineStyles>
        <header className="header">
          <Navigator />
        </header>
      </Headroom>
      {background}
      {this.props.children()}
      <Footer/>
      </div>
    )
  }
}

export default TemplateWrapper;


