import React from 'react';
import Link from 'gatsby-link'
import HeartLogo from 'react-icons/lib/fa/heart';
import Beerlogo from 'react-icons/lib/fa/beer';
import FeedbackLogo from '../images/opinionlab-orange.gif';
import LinkedinLogo from '../images/linkedIn.png';
import FacebookLogo from '../images/facebook.png';
import YoutubeLogo from '../images/YouTube.png';

export const Footer = () =>
  (<footer className="site-footer">
    <div className="container">
      <div className="row">
        <div className="col-md-9">
          <div className="footer-copyright">
            Built with <HeartLogo color='#FF0000' size='16'/> by M Shandilya. Various trademarks held by their respective owners.<br />
          </div>
          <div className="footer-links">
            <Link to="/language">Language</Link>
            <Link to="/resume">CV [Resume]</Link>
            <Link to="/Credit">Credit</Link>
            <a className="feedback" href="#">
              <img src={FeedbackLogo} alt="Feedback"/>
              Feedback
            </a>
          </div>
        </div>
        <div className="col-md-3">
          <ul className="social-icons unstyled">
            <li>
              <a href="https://www.facebook.com/madhav.shandilya.7" target="_blank"><img src={FacebookLogo} alt="Facebook"/></a>
            </li>
            <li>
              <a href="https://www.linkedin.com/in/expressinc/" target="_blank"><img src={LinkedinLogo} alt="Linkedin"/></a>
            </li>
            <li>
              <a href="https://www.youtube.com/channel/UCtSNWJDGqQwXUqmx9IaHEfQ?disable_polymer=true" target="_blank"><img src={YoutubeLogo} alt="Youtube"/></a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </footer>);

export default Footer;



